import React, { Fragment } from 'react';
import './Education.css'



const Education  = (props) => {
    const data = props.portfolioData;
    let eduList;
    eduList = data.education.map((edu, i) =>(<Fragment>  <div key={i} className="ui card">
       <div className="image">
       <img src={edu.img} alt=" let me check" />
   </div>
    <div className="content">
   
      <div className="header">{edu.title}</div>
      <div className="meta">
<span className="category">{edu.name}</span>
      </div>
      <div className="description">
      {edu.board}<br>
      </br>
      {edu.score}
      </div>
    </div>
 
    <div className="extra content">
      <div className="right floated author">
        
      </div>
    </div>
  </div>
    </Fragment>
  
))

    return(  <section className="site-section  site-section--with-back" id="education">
    <div className="container">
        <div className="site-section__icon">
          <svg xmlns="http://www.w3.org/2000/svg" width="33" height="7" viewBox="0 0 73.94 14.19">
            <polyline points="2.83 2.83 11.36 11.36 19.9 2.83 28.43 11.36 36.97 2.83 45.5 11.36 54.04 2.83 62.58 11.36 71.11 2.83" style={{ strokeDashoffset: "2e-05", strokeDasharray: "none" }}></polyline>
          </svg>
        </div>        
      <h1 className="site-heading"> Education  </h1>
      <div className="row">
    
    { eduList } 
              
</div>
    </div>
  </section>	 )
}
export default Education;